import React, { useState, useEffect } from "react";
import { GrStar } from "react-icons/gr";
import { FiEdit } from "react-icons/fi";
import { BsTrash } from "react-icons/bs";

const Review = ({ review, user }) => {
  const [edit, setEdit] = useState(false);
  const [stars, setStars] = useState("");
  const [editedText, setEditedText] = useState(review.text);
  const handleEdit = (key) => {
    setEdit(true);
  };
  const handleEditSub = (key) => {
    fetch(`/api/api/reviews/${key}`, {
      method: "PATCH",
      body: JSON.stringify({ text: editedText }),
      headers: { "Content-Type": "application/json" },
    })
      .then((resp) => resp.json())
      .then((d) => console.log(d));

    setEdit(false);
  };
  const handleDelete = (key) => {
    fetch(`/api/api/reviews/${key}`, {
      method: "DELETE",
    })
      .then((resp) => resp.json())
      .then((d) => console.log(d));

    setEdit(false);
    setEditedText("");
    setStars("");
  };
  const countStar = (num) => {
    let word = "";
    for (let i = 0; i < num; i++) {
      word += "";
    }
    return word;
  };
  useEffect(() => {
    let allStars = countStar(review.rating);
    setStars(allStars);
  }, []);
  console.log(review, user);
  return (
    <>
      <li className="review-list">
        <h4 className="review-name">{review.name} said:</h4>
        <section className="text">
          <h5>{editedText}</h5>
          <h5>{stars}</h5>
          <span style={{ float: "right", color: "green" }}>
            <FiEdit onClick={handleEdit} />
          </span>
          <span style={{ float: "right", color: "red" }}>
            <BsTrash
              onClick={() => {
                handleDelete(review.id);
              }}
            />
          </span>
        </section>

        {edit && (
          <form>
            <input
              value={editedText}
              type="text"
              onChange={(e) => {
                setEditedText(e.target.value);
              }}
            ></input>
          </form>
        )}

        {user.name === review.name && edit && (
          <button
            className="btn"
            onClick={() => {
              handleEditSub(review.id);
            }}
          >
            Submit Edited review
          </button>
        )}
      </li>
      <hr className="big-line"></hr>
    </>
  );
};

export default Review;
